<template>
  <el-table
    :data="tableData.data_all"
    style="width: 100%;" border>
    <el-table-column
      fixed
      label="序号"
      type="index"
      header-align="center"
      align="center"
      width="40"
    >
    </el-table-column>
    <el-table-column
      fixed
      prop="pri_pic"
      align="center"
      label="照片"
      header-align="center"
      width="90"
    >
      <template slot-scope="pic">
        <el-image :src="pic.row.pri_pic" style="width: 60px;height: 80px;" fit="cover"></el-image>
      </template>

    </el-table-column>
    <el-table-column
      fixed
      prop="student_id"
      align="center"
      label="学号"
      header-align="center"
      min-width="70"
    >
    </el-table-column>
    <el-table-column
      fixed
      prop="student_name"
      align="center"
      label="姓名"
      header-align="center"
      min-width="70"
    >
    </el-table-column>
    <el-table-column
      fixed
      prop="class_belong"
      align="center"
      label="班级"
      header-align="center"
      min-width="100"
    >
    </el-table-column>
    <el-table-column
      fixed
      prop="lock_state"
      align="center"
      label="锁屏情况"
      header-align="center"
      min-width="90"

    >
    </el-table-column>
    <el-table-column
      prop="every_register_sit"
      align="center"
      header-align="center"
      min-width="110"
      v-for="index_column of tableData.register_count" :key="index_column"
    >
      <template slot="header">
        <span>第{{index_column}}次</span>
        <el-select v-model="chooseOptionTotalValue" placeholder="请选择">
          <el-option
            v-for="item_choose in chooseOptions"
            :key="item_choose.value"
            :label="item_choose.label"
            :value="item_choose.value">
          </el-option>
        </el-select>
        <el-button class="el-button-local" type="primary" size="mini">删除</el-button>
        <el-button class="el-button-local" type="primary" size="mini">保存</el-button>
      </template>
      <template slot-scope="row_item">

        <el-select v-model="row_item.row.every_register_sit[index_column-1]" placeholder="请选择">
          <el-option
            v-for="item_choose in chooseOptions"
            :key="item_choose.value"
            :label="item_choose.label"
            :value="item_choose.value">
          </el-option>
        </el-select>
        <!--        <el-button class="el-button-local" @click="handleClick(scope.row)" type="primary" size="mini">锁屏</el-button>-->
        <!--        <el-button class="el-button-local" @click="handleClick(scope.row)" type="primary" size="mini">解锁</el-button>-->
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'component-table-student-manage',
  data () {
    return {
      tableData: {
        register_count: 13, // 签到的总次数
        class_count: '3DX0790A.01',
        class_name: '软件质量保证与测试',
        teacher_name: '阮啟东(2018750)',
        data_all: [{ // 表格数据
          pri_pic: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1586324454011&di=b6e0f65310fc99c70d6866202101698e&imgtype=0&src=http%3A%2F%2Fwww.99danji.com%2Fupload%2F20129%2F2012092951724937.jpg', // 学生照片
          student_id: '2018750', // 学生学号
          student_name: '阮啟东', // 学生姓名
          class_belong: '软件2017-01', // 学生班级
          lock_state: '已锁屏', // 当前锁屏状况
          every_register_sit: [// 若数组中改为对象，则会出现无法修改的现象，目前认为是bug
            // 单个学生的每次签到的出勤情况
            '1', // 出勤状态码  1出勤 2迟到 3 请假 4缺勤
            '3'
          ]
        },
        { // 第二个学生的所有信息
          pri_pic: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1586324392292&di=07aa95cc97210222ab0222bfbf99bed1&imgtype=0&src=http%3A%2F%2Fgss0.baidu.com%2F-fo3dSag_xI4khGko9WTAnF6hhy%2Fzhidao%2Fpic%2Fitem%2F0dd7912397dda1446b11f863b7b7d0a20df486c3.jpg',
          student_id: '2018750',
          student_name: '阮啟东2',
          class_belong: '软件2017-01',
          lock_state: '已锁屏',
          every_register_sit: [
            '1',

            '2'

          ]
        },

        { // 第三个学生的所有信息
          pri_pic: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3259856739,784554029&fm=26&gp=0.jpg',
          student_id: '2018750',
          student_name: '阮啟东23',
          class_belong: '软件2017-01',
          lock_state: '已锁屏',
          every_register_sit: [
            '1',

            '2'

          ]
        },
        { // 第三个学生的所有信息
          pri_pic: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3259856739,784554029&fm=26&gp=0.jpg',
          student_id: '2018750',
          student_name: '阮啟东23',
          class_belong: '软件2017-01',
          lock_state: '已锁屏',
          every_register_sit: [
            '1',

            '2'

          ]
        }
        ]},
      chooseOptions: [{
        value: '1',
        label: '出勤'
      }, {
        value: '2',
        label: '迟到'
      }, {
        value: '3',
        label: '请假'
      }, {
        value: '4',
        label: '缺勤'
      }],
      chooseOptionTotalValue: ''
    }
  }
}
</script>

<style scoped>

</style>



